"use client";

import Image from "next/image";
import Link from "next/link";
import { useCart } from "@/context/CartContext";

export default function ProductCard({ product }) {
  const { addToCart } = useCart();

  const handleAddToCart = () => {
    addToCart(product);
  };

  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-xl transition-shadow duration-300">
      <div className="relative h-64 w-full">
        <Image
          src={product.image}
          alt={product.name}
          fill
          priority
          className="object-cover"
          sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
        />
      </div>
      <div className="p-4">
        <h3 className="text-lg font-semibold text-gray-800 mb-2">{product.name}</h3>
        <p className="text-gray-600 mb-2 line-clamp-2">{product.description}</p>
        <div className="text-xl font-bold text-gray-900 mb-4">
          ${product.price.toFixed(2)}
        </div>
        <div className="flex space-x-2">
          <Link 
            href={`/product/${product.id}`}
            className="flex-1 bg-gray-800 text-white py-2 px-4 rounded-md hover:bg-gray-700 transition-colors text-center"
          >
            Buy Now
          </Link>
          <button
            onClick={handleAddToCart}
            className="flex-1 bg-white text-gray-800 py-2 px-4 rounded-md border border-gray-800 hover:bg-gray-100 transition-colors"
          >
            Add to Cart
          </button>
        </div>
      </div>
    </div>
  );
}
